.yc-link {
  text-decoration: none;
  touch-action: manipulation;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  cursor: pointer;
}
.yc-link:focus {
  outline: 2px solid var(--yc-color-line-misc);
}
.yc-link:focus:not(:focus-visible) {
  outline: 0;
}
.yc-link_view_normal {
  color: var(--yc-color-text-link);
}
.yc-link_view_normal:hover {
  color: var(--yc-color-text-link-hover);
}
.yc-link_view_primary {
  color: var(--yc-color-text-primary);
}
.yc-link_view_primary:hover {
  color: var(--yc-color-text-link-hover);
}
.yc-link_view_secondary {
  color: var(--yc-color-text-secondary);
}
.yc-link_view_secondary:hover {
  color: var(--yc-color-text-link-hover);
}
.yc-link_view_normal-visitable {
  color: var(--yc-color-text-link);
}
.yc-link_view_normal-visitable:visited {
  color: var(--yc-color-text-link-visited);
}
.yc-link_view_normal-visitable:hover {
  color: var(--yc-color-text-link-hover);
}
.yc-link_view_normal-visitable:visited:hover {
  color: var(--yc-color-text-link-visited-hover);
}