.yc-popup {
  --yc-popup-background-color: var(--yc-color-base-float);
  --yc-popup-border-color: var(--yc-color-line-solid);
  --yc-popup-border-width: 1px;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
  animation-duration: 0.1s;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
  border-radius: 4px;
  background-color: var(--yc-popup-background-color);
  visibility: hidden;
  box-shadow: 0 8px 20px var(--yc-color-sfx-shadow);
  outline: none;
}
.yc-popup::before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: inherit;
  box-shadow: 0 0 0 var(--yc-popup-border-width) var(--yc-popup-border-color);
}
.yc-popup > :first-child:not(.yc-popup__arrow), .yc-popup > .yc-popup__arrow + * {
  border-top-left-radius: inherit;
  border-top-right-radius: inherit;
}
.yc-popup > :last-child {
  border-bottom-left-radius: inherit;
  border-bottom-right-radius: inherit;
}
.yc-popup_open {
  visibility: visible;
}
.yc-popup[data-popper-placement*=bottom][data-inited] {
  animation-name: yc-popup-bottom;
}
.yc-popup[data-popper-placement*=top][data-inited] {
  animation-name: yc-popup-top;
}
.yc-popup[data-popper-placement*=left][data-inited] {
  animation-name: yc-popup-left;
}
.yc-popup[data-popper-placement*=right][data-inited] {
  animation-name: yc-popup-right;
}
.yc-popup_open[data-popper-placement*=bottom][data-inited] {
  animation-name: yc-popup-bottom-open;
}
.yc-popup_open[data-popper-placement*=top][data-inited] {
  animation-name: yc-popup-top-open;
}
.yc-popup_open[data-popper-placement*=left][data-inited] {
  animation-name: yc-popup-left-open;
}
.yc-popup_open[data-popper-placement*=right][data-inited] {
  animation-name: yc-popup-right-open;
}
.yc-popup[data-popper-placement*=bottom] .yc-popup__arrow {
  top: -9px;
}
.yc-popup[data-popper-placement*=top] .yc-popup__arrow {
  transform: rotate(180deg);
  bottom: -9px;
}
.yc-popup[data-popper-placement*=left] .yc-popup__arrow {
  transform: rotate(90deg);
  right: -9px;
}
.yc-popup[data-popper-placement*=right] .yc-popup__arrow {
  transform: rotate(-90deg);
  left: -9px;
}
.yc-popup__arrow-content {
  width: 18px;
  height: 18px;
  position: relative;
  overflow: hidden;
  display: flex;
}
.yc-popup__arrow-circle-wrapper {
  background-color: transparent;
  overflow: hidden;
  width: 8px;
  height: 9px;
  position: relative;
}
.yc-popup__arrow-circle {
  box-sizing: border-box;
  border-radius: 50%;
  box-shadow: inset 0 0 0 calc(5px - var(--yc-popup-border-width)) var(--yc-popup-background-color), inset 0 0 0 5px var(--yc-popup-border-color);
  width: 28px;
  height: 30px;
  position: absolute;
}
.yc-popup__arrow-circle_left {
  right: -5px;
  bottom: -4px;
}
.yc-popup__arrow-circle_right {
  left: -5px;
  bottom: -4px;
}
@keyframes yc-popup-bottom {
  0% {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }
  99% {
    transform: translateY(10px);
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}
@keyframes yc-popup-bottom-open {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes yc-popup-top {
  0% {
    visibility: visible;
    opacity: 1;
    transform: translateY(0);
  }
  99% {
    transform: translateY(-10px);
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}
@keyframes yc-popup-top-open {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes yc-popup-left {
  0% {
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
  }
  99% {
    transform: translateX(-10px);
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}
@keyframes yc-popup-left-open {
  0% {
    opacity: 0;
    transform: translateX(-10px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes yc-popup-right {
  0% {
    visibility: visible;
    opacity: 1;
    transform: translateX(0);
  }
  99% {
    transform: translateX(10px);
  }
  100% {
    visibility: hidden;
    opacity: 0;
  }
}
@keyframes yc-popup-right-open {
  0% {
    opacity: 0;
    transform: translateX(10px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}